import {GET_VIDEO_LIST} from "../../common/api/api.js";
export default {
	data() {
		return {
			showLoding:false,
			showMore:false,
			moreTip:"加载中...",
			requestCont:null,
			currentPage:1,
			nums:2,
			videoList:[],
			timer:null
		}
	},
	onLoad:function(){
		this.getVideoList()
	},
	//页面隐藏时暂停
	onHide:function(){
		if(this.videoContext){
			this.videoContext.pause()
		}
	},
	// 监听下拉刷新过程
	onPullDownRefresh() {
		this.showLoding = true;
	    this.getRes()    
	},
	// 监听上拉加载过程
	onReachBottom(){
		 // console.log("到底了,要重新拉取数据了")
		 clearTimeout(this.timer)
		 this.showMore = true;
		 this.currentPage++;
		 this.getVideoList()
	},
	methods: {
		//请求:获取video视频列表
		getVideoList(){
			this.requestCont = GET_VIDEO_LIST({currentPage:this.currentPage,nums:this.nums});
			this.requestCont.post().then(res=>{
				if(res.data.length>0){
					for(let val of res.data){
						val.showVideo = false;
						this.videoList.push(val)
					}
				}else{
					this.moreTip = "没有更多了";
					this.timer = setTimeout(()=>{
						this.showMore = false;
						this.moreTip = "加载中...";
					},500)
				}
			})
		},
		// 刷新数据
		getRes(){
			setTimeout(()=> {
			    uni.stopPullDownRefresh()
				this.showLoding = false;
			}, 1000);
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.target.errMsg || "该环境不支持播放此视频",
				showCancel: false
			})
		},
		whereVideo(index){
			let nowItem = this.videoList;
			for(let i=0;i<nowItem.length;i++){
				if(i==index){
					nowItem[i].showVideo = true;
				}else{
					nowItem[i].showVideo = false;
				}
			}
			this.videoList = nowItem;

			this.videoContext = uni.createVideoContext('myVideo'+index);
			this.videoContext.play();

		}
	}
}
